@import "wundership.less";

.bluebox, .orangebox, .successbox, .infobox, .warningbox{

}

.col-md-6, .col-md-12{
  padding: 0px;
}

/* BLUE */

.bluebox{
  width: 100%;
  background-color: white;
  border: 1px solid #00B3FF;
  margin-bottom: 20px;
  .header{
  width: 100%;
  padding: 15px;
  color: white;
  background-color: #00B3FF;
  border-bottom: 1px solid #00B3FF;
  }
  .content{
    padding: 15px; 
  }
}

/* BLUE */

.orangebox{
  width: 100%;
  background-color: white;
  border: 1px solid #ffc000;
  margin-bottom: 20px;
  .header{
  width: 100%;
  padding: 15px;
  color: white;
  background-color: #ffc000;
  border-bottom: 1px solid #ffc000;
    p{
      color: white;
      margin-top: 2px;
    }
    i{
      font-size: 30px;
    }
  }
  .content{
    padding: 15px; 
  }
  p{
      color: @dark_grey;
  }
}

/* SUCCESS */

.successbox{
  width: 100%;
  background-color: white;
  border: 1px solid #52b856;
  margin-bottom: 20px;
  .header{
  width: 100%;
  padding: 15px;
  color: white;
  background-color: #52b856;
  border-bottom: 1px solid #52b856;
    p{
      color: white;
      margin-top: 2px;
    }
    i{
      font-size: 30px;
    }
  }
  .content{
    padding: 15px; 
    .info_text{
      color: @dark_grey;
    }
    p{
      color: @dark_grey;
    }
  }
}

/* INFO */

.infobox{
  width: 100%;
  background-color: white;
  border: 1px solid #ababab;
  margin-bottom: 20px;
  .header{
  width: 100%;
  padding: 15px;
  background-color: #e8e8e8;
  border-bottom: 1px solid #ababab;
    p{
      color: @bright_grey;
    }
  }
  .content{
    padding: 15px;
    p{
      color: @base;
    }
    .info_text{
      color: @dark_grey;
    }
  }
  
}

/* INFO IMAGE */

.infobox_image{
  background-color: white;
  border: 1px solid #ababab;
  margin-top: 20px;
  height: 302px;
  margin-bottom: 20px;
  .header{
  height: 60px;
  padding: 15px;
  width: 325px;
  background-color: #e8e8e8;
  /*border-bottom: 1px solid #ababab;*/
    p{
      color: @bright_grey;
    }
  }
  .content{
    padding: 15px;
    p{
      color: @base;
    }
    .info_text{
      color: @dark_grey;
    }
  }
  .container{
    width: 300px;
    background-color: grey;
    height: 300px;
    background-size: cover;
  }
}


/* WARNING */

.warningbox{
  width: 100%;
  background-color: white;
  border: 1px solid @base;
  margin-bottom: 20px;
  .header{
  width: 100%;
  padding: 15px;
  color: white;
  background-color: @base;
  border-bottom: 1px solid @base;
    p{
      color: white;
      margin-top: 2px;
    }
    i{
      font-size: 30px;
    }
  }
  .content{
    padding: 15px; 
  }
  p{
      color: @dark_grey;
  }
}

/* ACTION */

.actionbox{
  max-width: 200px;
  background-color: @first;
  margin-top: 10px;
  padding: 20px;
  position: relative;
  margin-bottom: 20px;
  border: 1px solid #ababab;
  i{
  font-size: 40px;
  color: @first_com;
  }
  h4{
    color: @first_com;
  }
  p{
    margin-top: 10px;
    color: @first_com;
  }
}


/* ACTIONBOX TALL */

.tall_actionbox{
  max-width: 200px;
  background-color: @base;
  margin-top: 20px;
  padding: 20px;
  position: relative;
  margin-bottom: 20px;
  border: 1px solid #ababab;
  i{
  font-size: 40px;
  color: @base_com;
  }
  h4{
    color: @base_com;
  }
  p{
    color: @base_com;
  }
}

/* USER INFOBOX*/

.user_infobox{
  background-color: white;
  max-width: 202px;
  text-align: center;
  padding-top: 5px;
  padding-bottom: 20px;
  border-bottom: 1px solid #ababab;
  border-left: 1px solid #ababab;
  border-right: 1px solid #ababab;
}



/* first image container */
.infobox_large_image{
  background-color: white;
  border: 1px solid #ababab;
  margin-bottom: 20px;
  .left{
    .header{
      position: top;
      height: 60px;
      padding: 15px;
      background-color: #e8e8e8;
      /*border-bottom: 1px solid #ababab;*/
        p{
          color: @bright_grey;
        }
    }

    .content{
      padding: 15px;
      .info_text{
        color: @dark_grey;
      }
      p{
        color: @base;
      }
    }
  }
  .right{
    .content{
    }
  }
}

.header-icons{
  vertical-align: sub;
  margin-right: 20px;
}

@media (min-width: 992px) {
  .header{
    height: 60px;
  }
}

@media (max-width: 767px){
  .row{
    margin-left: 5px;
    margin-right: 5px;
  }
}